.partners {
    position: relative;
    padding-bottom: 145px;
    @media (max-width: $m) {
        padding-bottom: 170px;
    }
    .cilrcle-3 {
        top: -15%;
    }
}

.partners__title {
    margin-bottom: 67px;
    @media (max-width: $x) {
        margin-bottom: 30px;
    }
}

.partners__slider {
    position: relative;
}
.partners__swiper {
    .swiper-wrapper {
        align-items: center;
    }
}
.swiper {
}

.partners__slide {
    @extend .flex-center;
}
.partners__slide-img {
    cursor: pointer;
    filter: grayscale(1);
    transition: 0.3s ease-in;
    &:hover {
        filter: grayscale(0);
    }
}
.partners__controls {
    top: -130px;
    right: 60px;
    position: absolute;
    @media (max-width: $m) {
        top: 95px;
        right: 50%;
    }
}
.controls {
}
.partners-prev {
}
.prev {
    right: 10px;
}
.swiper-btn {
}
.partners-next {
}
.next {
}
