.product-card {
    padding-bottom: 20px;
    .title-line {
        padding-bottom: 9px;
        &::before {
        position: absolute;
        content: '';
            left: 0;
            transform: translateX(0);
            width: 540px;
            @media (max-width: $l) {
                @include calc(width, 270, 540);
            }
        }
    }
    .cilrcle-3 {
        top: 20%;
    }
}
.container {
}
.product-card__title {
    margin-bottom: 30px;
    width:max-content;
    
}

.product-card__contaier {
    display: grid;
    grid-template-columns: 1fr 1fr;
    @media (max-width: $mn) {
        grid-template-columns: 1fr;
    }
    .section-catalog-card__inner {
        background: #f8f8fa;
        padding: 20px 60px 28px 50px;
        margin-bottom: 20px;
        @media (max-width: $xx) {
            padding: 20px;
        }
    }
    .section-catalog-card {
        box-shadow: unset;
        background-color: #ffffff;
        
    }
    .section-catalog-card--not-padding {
        padding: 0 0 20px 0;
    }
    .section-catalog-card__holder-image {
        padding: 40px 20px 65px;
    }
    .section-catalog-card__img {
    }
    .section-catalog-card__list {
        border-top: 1px solid rgba(88, 88, 92, .2);
        border-bottom: 1px solid rgba(88, 88, 92, .2);
        padding: 18px 0px 18px 30px;
        margin-bottom: 30px;
    }
    .card-info {
    }
    .card-info__item {
        &:not(:last-child) {
            margin-bottom: 15px;
        }
    }
    .card-info__text {
        width: 90%;
    }
    .card-info__num {
    }
}

.section-catalog-card__box-link {
    display: flex;
    align-items: center;
}
.section-catalog-card__link {   
    font-size: 17px;    
    font-weight: 400;
    color: #24558d;
    margin-right: 15px;
}
.section-catalog-card__icon-link {
}

.section-catalog-card__footer {
    display: grid;
    grid-template-columns: minmax(auto, 300px) minmax(auto, 300px);
    gap: 0 15px;
    width: 100%;
    padding-right: 20px;
    @media (max-width: $mn) {
        grid-template-columns: 1fr 1fr;
        padding-left: 20px;
    }
    @media (max-width: $xx) {
        grid-template-columns: 1fr;
        gap: 10px 0px;
    }
}
.section-catalog-card__btn {
    padding: 25px 95px;
    display: grid;
    align-content: center;
    @media (max-width: $l) {
        padding: 25px 55px;
    }
}

.section-catalog-card__btn-delevery {
    position: relative;
    display: grid;
    align-content: center;
    font-size: 17.3px;
    line-height: 24px;
    font-weight: 400;
    color: #3a3a3a;
    background-image: url('./../img/icons/icon_delivery_truck.png');
    background-repeat: no-repeat;
    background-position: 60px 17px;
    background-size: auto;
    padding: 10px 30px 10px 120px;  
    border: 1px solid #d2d2d4;
    border-radius: 3px;
    background-color: #fff;
    @media (max-width: $l) {
        padding: 10px 15px 10px 70px;
        background-position: 10px 17px;
    }
    @media (max-width: $xx) {
        padding: 20px 15px 20px 80px;
    }
}