.section-form {
    position: relative;
	padding-bottom: 70px;
	@media (max-width: $xx) {
		padding-bottom: 40px;
	}
    .cilrcle-1 {
        left: 0;        
        top: 41%;
    }
    .cilrcle-3 {
        right: 0;
        top: 65%;
    }
}

.form {
    position: relative;
}
.form-holder {
    background-image: linear-gradient(
        90deg,
        rgb(95, 143, 198) 0%,
        rgb(4, 151, 205) 0%,
        rgb(107, 69, 233) 0%,
        rgb(190, 171, 167) 0%,
        rgb(116, 154, 250) 100%
    );
    border-radius: 20px;
    padding: 20px 20px 30px;
}
.form-holder__title {
    margin-bottom: 10px;
    h2 {
        color: #ffffff;
    }
}

.form-holder__subtitle {
    font-family: var(--font-m);
    font-size: 16px;
    font-weight: 400;
    color: #4f4f4f;
    margin-bottom: 30px;
	@media (max-width: $x) {
		font-size: 14px;
		margin-bottom: 15px;
	}
}
.form-holder__container {
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(5, 58px);
    gap: 10px 20px;
	margin-bottom: 30px;
	@media (max-width: $xx) {
		grid-template-columns: 1fr;
		grid-template-rows: repeat(10, 58px);
		grid-auto-flow: row;
		gap: 10px 0px;
	}
}
.form-block {
    position: relative;
    width: 100%;
    height: 100%;
	&::before {
		position: absolute;
		content: '';
		background-repeat: no-repeat;       
        background-size: auto;
		top: 50%;
		left: 20px;
		transform: translateY(-50%);
		@media (max-width: $x) {
			left: 10px;
		}
	}
}

.form-input {
    background: #e5e5e5;
    border-radius: 8px;
    width: 100%;
    height: 100%;
    border: none;
    outline: none;
    font-family: var(--font-m);
    font-size: 18px;
    line-height: 1.2;
    font-weight: 400;
    color: #000;
    padding: 10px 10px 10px 60px;
	@media (max-width: $x) {
		padding: 10px 10px 10px 40px;
		font-size: 16px;
	}
    &:focus {
        outline: none;
    }
    &::placeholder {
        color: rgba(52, 52, 52, 0.4);
    }
}
.form-block--marka {
    &::before {       
        background-image: url("./../img/icons/f_cube.png");        
		width: 20px;
		height: 22px;		
    }
    .form-input {
        &::placeholder {
            font-weight: 700;
            color: var(--color-b);
        }
    }
}
.form-block--inn {
	&::before {       
        background-image: url("./../img/icons/f_list.png");        
		width: 24px;
		height: 26px;		
    }
}
.form-block--user {
	&::before {       
        background-image: url("./../img/icons/f_user.png");        
		width: 16px;
		height: 18px;		
    }
}
.form-block--tel {
	&::before {       
        background-image: url("./../img/icons/f_phone.png");        
		width: 17px;
		height: 17px;		
    }
}
.form-block--city {
	&::before {       
        background-image: url("./../img/icons/f_map.png");        
		width: 14px;
		height: 20px;		
    }
}
.form-block--company {
	&::before {       
        background-image: url("./../img/icons/f_build.png");        
		width: 21px;
		height: 22px;		
    }
}
.form-block--weight {
	&::before {       
        background-image: url("./../img/icons/f_kg.png");        
		width: 21px;
		height: 23px;		
    }
}
.form-block--message {
	&::before {       
        background-image: url("./../img/icons/f_message.png");        
		width: 20px;
		height: 22px;
		top: 23px;		
    }
    grid-row: 3/5;
	@media (max-width: $xx) {
		grid-row: 8/10;
	}
}
.form-input--message {
    resize: none;
}

.form-button {
}
.send-button {
    cursor: pointer;
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 8px;
    background-image: linear-gradient(
        90deg,
        rgb(124, 172, 236) 0%,
        rgb(54, 126, 239) 0%,
        rgb(190, 171, 167) 0%,
        rgb(95, 143, 198) 0%,
        rgb(36, 85, 141) 100%
    );
    outline: none;
    font-size: 18px;
    line-height: 20px;
    font-weight: 400;
    color: #ffffff;
    &:focus {
        outline: none;
    }
    &:hover {
        background-image: linear-gradient(90deg, #5f8fc6 0%, #0497cd 100%);
    }
}

.form-holder__checkbox {
	@extend .flex-center;
}
.checkbox {
    display: flex;
	align-items: center;
	// width: max-content;
}
.checkbox__real-input {
    width: 0;
    height: 0;
    opacity: 0;
    position: absolute;
    z-index: -1;
}
// .checkbox__real-input:focus + .checkbox__custom-input {
//     box-shadow: 0px 0px 0px 1px #38577a, 0px 0px 10px #7e9bbd;
// }
.checkbox__custom-input {
    position: relative;
    margin-right: 10px;
    width: 18px;
	height: 18px;
    background-image: linear-gradient(180.0deg, #3c2525 0%, #db1528 100%);
	border: 1px solid #6c6c6c;
	border-radius: 5px;
    flex-shrink: 0;
}
.checkbox__text {
    font-family: var(--font-m);
	font-size: 15px;
	line-height: 1.2;
	font-weight: 300;
	color: #000000;
	a {
		text-decoration: underline;
		color: #000000;
	}
}
.checkbox__custom-input::before {
    content: "";
    display: inline-block;
    width: 17px;
    height: 17px;
    background-image: url("./../img/icons/check.png");
    background-size: contain;
    background-repeat: no-repeat;
    position: absolute;
	left: 60%;
    top: 34%;
    transform: translate(-50%, -50%) scale(0);
    transition: 0.2s ease-in;
}
.checkbox__real-input:checked + .checkbox__custom-input::before {
    transform: translate(-50%, -50%) scale(1);
}
