.section-contacts {
    position: relative;
    margin-top: -190px;
   @media (max-width: $xx) {
    margin-top: -175px;
   }
}
.contacts__container {
    display: flex;
    align-items: flex-end;
    padding-left: max(15px, (100% - 1300px) / 2 );
    @media (max-width: 1240px) {
        width: 100%;
        padding-left: 15px;
        padding-right: 15px;

        flex-direction: column;
    }
}
.contacts__block {

    padding: 24px 110px 35px 95px;
    background: #fff;
    .title-line {
        &::before {
            position: absolute;
            content: '';
            width: 184px;
        }
    }
    @media (max-width: 1240px) {
        width: 100%;
        padding: 24px 110px 50px 95px;
    }
    @media (max-width: $xx) {
        padding: 20px 40px 30px 40px;
    }
    @media (max-width: $x) {
        padding: 20px 20px 30px 20px;
    }
}
.contacts__title {
    margin-bottom: 70px;
    width: max-content;
    @media (max-width: 1240px) {
        margin-bottom: 30px;
    }
    @media (max-width: $x) {
        text-align: center;
        width: 100%;
    }
}

.contacts__body {
    margin-bottom: 70px;
    width: 290px;
    @media (max-width: 1240px) {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 30px;
    }
    @media (max-width: $l) {
        flex-wrap: wrap;
        gap: 20px;
    }
}
.contacts__link-row {
    display: flex;
    align-items: center;    
    &:not(:last-child) {
        margin-bottom: 23px;
        @media (max-width: 1240px) {
            margin-bottom: 0;
            // margin-right: 20px;
        }
    }
    @media (max-width: $l) {
        width: 47%;
    }
    @media (max-width: $mn) {
        width: 100%;
    }
}
.contacts__link-icon {
    margin-right: 25px;
}
.contacts__link-text {
    font-family: var(--font-m);
    font-size: 20px;  
    font-weight: 600;
    color: #2d2d2d;
    @media (max-width: $xs) {
        font-size: 16px;
    }
}
.contacts__social {
    display: flex;
    justify-content: space-between;
    padding-right: 25px;
    
}
.contacts__icon-social {
}
.contacts__block-map { 
    position: relative;   
    width: 100%;
    height: 433px;
}

.contacts__tabs-list {
    position: absolute;
    display: flex;
    top: -25px;
    left: -10px;
    @media (max-width: $mn) {
        left: 0px;
        
        // flex-direction: column;
    }
    @media (max-width: $xx) {
        position: static;
        display: grid;
        grid-template-columns: 1fr 1fr;
        border: 1px solid #fff;
    }
    @media (max-width: 520px) {
        grid-template-columns:  1fr;
    }
}

.contacts__tab-item {
    position: relative;
    padding: 33px 78px 33px 75px;
    @media (max-width: $m) {
        padding: 23px 25px 23px 30px;
    }
    @media (max-width: $xx) {
        padding: 20px 20px;
        // justify-self: center;
    }
    span {
        position: relative;
        font-family: var(--font-m);
        font-size: 16px;     
        font-weight: 600;
        color: #2d2d2d;
        @media (max-width: $xx) {
            color: #fff;
        }
    }
    &:first-child {
        z-index: 1;
        @media (max-width: $xx) {
            border-right: 1px solid #fff;
        }
        @media (max-width: 520px) {
            border-right: unset;
        }
        &::before {
            position: absolute;
            content: '';
            background-image: url('./../img/fon_white_1.png');
            background-repeat: no-repeat;
            background-position: center;
            background-size: auto;
            width: 398px;
            height: 87px;
            top: 0px;
            left: 0;
            @media (max-width: $m) {
                background-size: 288px;
                width: 290px;
                height: 70px;
            }
            @media (max-width: $xx) {
                display: none;
            }
        }
        &.active {
            &::before {
               
                background-image: url('./../img/fon_blue_1.png');
                @media (max-width: $xx) {
                    display: none;
                }
               
            }
          @media (max-width: $xx) {
            background-image: linear-gradient(90deg, rgb(95, 143, 198) 0%, rgb(4, 151, 205) 0%, rgb(107, 69, 233) 0%, rgb(190, 171, 167) 0%, rgb(116, 154, 250) 100%);
          }
            span {
                color: #fff;
            }
        }
    }
    &:last-child {
        &::before {
            position: absolute;
            content: '';
            background-image: url('./../img/fon_white.png');
            background-repeat: no-repeat;
            background-position: center;
            background-size: auto;
            width: 373px;
            height: 85px;
            top: 0px;
            left: 0;
            @media (max-width: $m) {
                background-size: 290px;
                width: 290px;
                height: 70px;
            }
            @media (max-width: $xx) {
                display: none;
            }
        }
        &.active {
            &::before {
                position: absolute;
                content: '';
                background-image: url('./../img/fon_blue_1.png');
                width: 398px;
                top: 1px;
                @media (max-width: $m) {
                    background-size: 288px;
                    width: 290px;
                    height: 70px;
                }
                @media (max-width: $xx) {
                    display: none;
                }
              
            }
           @media (max-width: $xx) {
            background-image: linear-gradient(90deg, rgb(95, 143, 198) 0%, rgb(4, 151, 205) 0%, rgb(107, 69, 233) 0%, rgb(190, 171, 167) 0%, rgb(116, 154, 250) 100%);
           }
            span {
                color: #fff;
            }
        }
    }
    
}
.active {
}
.tabs-content {
}
.contacts__tabs-content {

    iframe {
        height: 433px;
        width: 100%;
    }
}