.product-info {
    position: relative;
    padding-bottom: 50px;
    .cilrcle-7 {
        top: 20%;
        left: 5%;
    }
}
.product-info__block {
    position: relative;
    background-color: #ffffff;
    // border: $border;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.1);
}
.product-info__list {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    border-bottom: 1px solid rgba(88, 88, 92, 0.5);
    box-shadow: 0px 0px 9px 4px rgba(127, 132, 137, 0.31);
    @media (max-width: $m) {
        grid-template-columns: repeat(4, 1fr);
    }
    @media (max-width: $xx) {
        grid-template-columns: 1fr;
    }
}

.product-info__item {
    cursor: pointer;
    padding: 19px 0px;
    text-align: center;
    font-weight: 400;
    font-family: var(--font-m);
    font-size: 18px;
    line-height: 23px;
    font-weight: 800;
    color: var(--color-b);
    @media (max-width: $mn) {
        font-size: 14px;
    }
    @media (max-width: $m) {
        padding: 10px 0px;
        border-bottom: 1px solid rgba(88, 88, 92, 0.5);
    }

    @media (max-width: $x) {
        font-size: 12px;
    }
    &.active {
        background-image: linear-gradient(
            0deg,
            rgb(95, 143, 198) 0%,
            rgb(4, 151, 205) 0%,
            rgb(107, 69, 233) 0%,
            rgb(190, 171, 167) 0%,
            rgb(116, 154, 250) 100%
        );
        color: #ffffff;
    }
    &:not(:last-child) {
        border-right: 1px solid rgba(88, 88, 92, 0.5);
        @media (max-width: $xx) {
            border-right: unset;
        }
    }
    &:nth-child(3) {
        @media (max-width: $xx) {
            border-right: none;
        }
    }
    &:nth-child(4) {
        @media (max-width: $m) {
            border-right: none;
        }
        @media (max-width: $xx) {
            grid-column: unset;
            // border-right: $border;
            // border-bottom: $border;
        }
    }
    // &:last-child {
    //     @media (max-width: $m) {
    //         border-bottom: none;
    //         grid-column: 1/5;
    //     }
    //     @media (max-width: $xx) {
    //         grid-column:unset;
    //     }
    // }
}

.product-info__content {
    font-size: 17px;
    line-height: 24px;
    font-weight: 400;
    padding: 30px 55px 40px;
    @media (max-width: $l) {
        padding: 30px 50px 40px;
    }
    @media (max-width: $m) {
        padding: 20px 30px;
    }

    .card-info {
    }
    .card-info__item {
        &:nth-child(odd) {
            .card-info__text,
            .card-info__num {
                background: rgba(223, 226, 238, 0.3);
            }
        }
        &::before {
            position: absolute;
            content: "";
            left: 40px;
            @media (max-width: $x) {
                left: 8px;
            }
        }
    }
    .card-info__text {
        width: 100%;
        padding-left: 80px;
        padding-top: 7px;
        padding-bottom: 7px;
        @media (max-width: $x) {
            line-height: 20px;
            padding-left: 30px;
        }
    }
    .card-info__num {
        padding-right: 55px;
        @media (max-width: $x) {
            padding-right: 15px;
        }
    }
}
.product-info__content--not-padding {
    padding: 30px 0px 40px;
    @media (max-width: $l) {
        padding: 30px 0px 40px;
    }
    @media (max-width: $m) {
        padding: 20px 0px 20px;
    }
}
.product-info__content--p {
   @media (max-width: $xx) {
    padding: 10px;
   }
}
.product-info__desc {
    margin-bottom: 25px;
    @media (max-width: $xx) {
        font-size: 16px;
    }
    @media (max-width: $x) {
        font-size: 14px;
    }
    @media (max-width: $xs) {
        font-size: 12px;
    }
    p {
        &:not(:last-child) {
            margin-bottom: 30px;
            @media (max-width: $xx) {
                margin-bottom: 15px;
            }
        }
    }
}
.product-info__title {
    font-weight: 800;
    color: var(--color-b);
    margin-bottom: 30px;
    @media (max-width: $xx) {
        margin-bottom: 20px;
    }
}
.product-info__title--pl {
    padding-left: 45px;
    @media (max-width: $m) {
        padding-left: 0;
    }
}
.product-info__content-list {
}
.product-info__content-item {
    font-size: 16px;
    line-height: 1.2;
    font-weight: 300;
    color: #505050;
    padding-left: 40px;
    background-image: url("./../img/icons/arrow_small_right.png");
    background-repeat: no-repeat;
    background-position: 0px 0px;
    background-size: 15px;
    @media (max-width: $x) {
        font-size: 14px;
        padding-left: 20px;
        background-position: 0px 0px;
    }
    &:not(:last-child) {
        margin-bottom: 20px;
        @media (max-width: $xx) {
            margin-bottom: 10px;
        }
    }
}

.product-info__sertificate-inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 35px 40px;
    @media (max-width: $mn) {
        gap: 20px;
    }
    @media (max-width: $xx) {
        gap: 10px;
    }
}
.product-info__sertificate-link {
    border: 1px solid #cbcbcb;
}
.product-info__sertificate-img {
}









.product-info__content-list--feature {
    margin-bottom: 40px;
    @media (max-width: $xx) {
        margin-bottom: 20px;
    }
    .product-info__content-item {
        padding: 8px 10px 5px 40px;
        background-position: 10px 10px;
        &:not(:last-child) {
            margin-bottom: 0;
        }
        &:nth-child(even) {
            background-color: #f8f8fa;
        }
    }
}
.table-inner {
    @media (max-width: $l) {
        overflow-x: scroll;
    }
}
.product-info__content {
    table {
        width: 100%;
        border-collapse: collapse;
        @media (max-width: $l) {
            overflow-x: scroll;
        }
    }
    th {
        font-size: 15.1px;
        line-height: 18px;
        font-weight: 500;
        color: #2a2a2a;
        border: 1px solid #868686;
        text-align: center;
        background: #f8f8fa;
        padding: 40px 5px;
    }
    tr {
        &:nth-child(2) {
            td {
                font-size: 15.1px;
                line-height: 18px;
                font-weight: 500;
                color: #2a2a2a;
                background: #f8f8fa;
                padding: 40px 5px;
            }
        }
        &:nth-child(even) {
            background: #f8f8fa;
        }
    }
    td {
        font-size: 14.1px;
        line-height: 18px;
        font-weight: 400;
        color: #868686;
        border: 1px solid #868686;
        text-align: center;
        padding: 20px 10px;
        img {
            display: inline;
        }
    }
}

//======================SLIDER===================

.product-info__slider {
    position: relative;
    max-width: 860px;
    margin-left: auto;
    margin-right: auto;
    @media (max-width: $xs) {
        max-width: 200px;
    }
    &:not(:last-child) {
        margin-bottom: 25px;
    }
}
.product-info__swiper {
}
.product-info__controls {
    .btn-slider {
        position: absolute;
        top: 50%;
        // transform: translateY(-50%);
        z-index: 1;
    }
    .btn-slider-prev {
        left: -50px;
        @media (max-width: $m) {
            left: -25px;
        }
    }
    .btn-slider-next {
        right: -50px;
        @media (max-width: $m) {
            right: -25px;
        }
    }
}

//========================accardion========================
.delivery {
    font-size: 17px;
    line-height: 1.2;
    font-weight: 400;
    .list__item-hidden.hidden {
        padding-left: 0;
        padding-top: 0;
    }
    .product-info__title {
        font-size: 17px;
        line-height: 1.2;
        font-weight: 400;
        color: #000000;
    }
}
.list__item{
    &:not(:first-child) {
        margin-top: 20px;
    }
        
    
    
}
.delivery__list--type {
    .delivery__item {
        position: relative;
        &::before {
            position: absolute;
            content: "";
            width: 5px;
            height: 1px;
            background: #000;
            top: 50%;
            left: -10px;
        }
    }
}
.delivery__row {
    display: flex;
    align-items: center;
    position: relative;
    margin-bottom: 15px;
}
.delivery__row--baseline {
    align-items: baseline;
}
.delivery__row-image {
    margin-right: 15px;
}
.delivery__row-image--opacity {
    opacity: 0.25;
}
.delivery__title {
    
    color: #24558d;
    margin-right: 40px;
    border-bottom: 1px dashed;
}
.delivery__check {
    flex: 0 0 auto;
    transition: transform 0.3s ease-in;
}
.delivery__check.show {
    transform: rotateX(-180deg);
}
.delivery__content {
    padding-left: 50px;
    
    font-size: 16px;
    @media (max-width: $x) {
        padding-left: 0px;
        font-size: 14px;
    }
}
.list__item-hidden {
    &:not(:last-child) {
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(134, 134, 134, .4);
        @media (max-width: $x) {
            padding-bottom: 0px;
        }
    }
    
}
.delivery__desc {
    margin-bottom: 30px;
    
    @media (max-width: $x) {
        margin-bottom: 20px;
    }
    p {
        &:not(:last-child) {
            margin-bottom: 30px;
            @media (max-width: $x) {
                margin-bottom: 20px;
            }
        }
    }
}

.delivery__box-row {
    padding-left: 45px;
    @media (max-width: $m) {
        padding-left: 0px;
    }
}
.delivery__row-partners {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 5px;
    margin-bottom: 5px;
    border-bottom: 1px solid rgba(134, 134, 134, .4);
    @media (max-width: $xx) {
        flex-direction: column;
        align-items: start;
    }
}
.delivery__row-name {
    display: flex;
    align-items: center;
    @media (max-width: $xx) {
        margin-bottom: 5px;
        // align-items: start;
        // flex-direction: column;
    }
}
.delivery__inner-img {
    border: 1px solid #868686;
    width: 71px;
    height: 48px;
    @extend .flex-center;
    margin-right: 20px;
    @media (max-width: $m) {
        margin-right: 10px;
        flex-shrink: 0;
    }
}
.delivery__row-img {
}
.delivery__row-title {
    font-size: 16px;
    @media (max-width: $xx) {
        font-size: 14px;
    }
}
.delivery__row-link {
    font-size: 16px;
    text-decoration: underline;
    color: var(--color-b);
    @media (max-width: $xx) {
        font-size: 14px;
    }
}
