.bredcrumbrs {
    display: flex;
    align-items: center;
    padding: 20px 0 12px;
    overflow-x: hidden;
    white-space: nowrap;
    
    @media (max-width: 424px) {
        padding: 10px 0 10px;
    }
}

.bredcrumbrs__item {
    display: flex;
    align-items: center;
    &:first-child {
        .bredcrumbrs__link {
            padding-left: 0;
        }
    }
    &:last-child {        
        .bredcrumbrs__link {
            text-decoration: underline;
            color: var(--color-b);
            cursor: auto;
        }
    }
    &:not(:last-child) {
        .bredcrumbrs__link {            
            &::before {
            position: absolute;
            content: '';
                width: 2px;
                height: 13px;
                background: #58585c;
            }
        }
    }    
}
.bredcrumbrs__icon {
    width: 25px;
    height: 21px;
    margin-right: 15px;
    @media (max-width: $xx) {
        display: none;
    }
}
.bredcrumbrs__link {
    
    position: relative;
    padding: 0 20px;
    font-family: var(--font-m);
    font-size: 16px;
    // line-height: 20px;
    font-weight: 600;
    color: #58585c;    
    @media (max-width: 665px) {
        padding: 0px 10px;
        font-size: 14px;
    }
    @media (max-width: 424px) {
        font-size: 12px;
    }
    @media (max-width: 374px) {
        padding: 0px 9px;
        font-size: 9px;
    }
    &::before {
        position: absolute;
        content: '';
        top: 50%;
        transform: translateY(-50%);
        right: 0;
    }
}