.footer {
    position: relative;
    background-image: linear-gradient(
        90deg,
        rgb(124, 172, 236) 0%,
        rgb(54, 126, 239) 0%,
        rgb(190, 171, 167) 0%,
        rgb(95, 143, 198) 0%,
        rgb(36, 85, 141) 100%
    );
    padding-top: 100px;
}

.footer__nav {
    margin-bottom: 10px;
    @media (max-width: $m) {
        display: none;
    }
    .nav {
        &::before {
            position: absolute;
            content: "";
            display: none;
        }
    }
    .nav__list {
    }
    .nav__item {
    }
    .nav__link {
        font-size: 16px;
        line-height: 20px;
        font-weight: 600;
    }
}

.footer__row-logo {
    display: flex;
    align-items: end;
    justify-content: space-between;
    padding-right: 55px;
    margin-bottom: 40px;
    @media (max-width: $t) {
        padding-right: 0px;
    }
    @media (max-width: $xx) {
        flex-direction: column;
        align-items: flex-start;
        padding-top: 135px;
        max-width: max-content;
        margin-left: auto;
        margin-right: auto;
    }
}
.footer__logo {
    @media (max-width: $xx) {
        margin-bottom: 10px;
    }
}
.footer__copyright {
    display: flex;
    align-items: center;
    margin-bottom: -15px;
    width: 302px;
  
  
}
.footer__copyright-img {
    margin-right: 15px;
    @media (max-width: 424px) {
        margin-right: 10px;
    }
}
.footer__copyright-text {
}
.footer__copyright-link {
    font-family: var(--font-o);
    font-size: 14px;
    line-height: 1.2;
    font-weight: 400;
    color: #000000;
    transition: 0.3s ease-in;
}
.footer__copyright-name {
    font-size: 14px;
    font-weight: 500;
    text-decoration: underline;
    color: #fff;
}
.footer-copyright {
    position: relative;
    font-family: var(--font-o);
    font-size: 16px;
    line-height: 24px;
    font-weight: 400;
    color: #ffffff;
    text-align: center;
    padding: 10px;
    z-index: 1;
    &::before {
        position: absolute;
        content: "";
        width: 100%;
        height: 100%;
        background-image: linear-gradient(90deg, #7cacec 0%, #367eef 100%);
        opacity: .5;
        top: 0;
        left: 0;

    }
    p {
        position: relative;
    }
}

.footer--product {
    padding-top: 10px;
    .footer__row-logo {
        @media (max-width: $xx) {
            padding-top: 15px;
        }
    }
}