.scroll-top {
    position: fixed;
    bottom: 150px;
    right: 50px;
    z-index: 1;
    opacity: 1;
    transition: .4s;
    img {
        width: 62px;
        height: 62px;
    }
    @media (max-width: $xx) {    
        bottom: 100px;
        right: 15px;    
    }
    &.scroll-top--hide {  
        opacity: 0;
    }
}